import * as React from 'react';
import { clearStyles } from '../actions';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

export function MobileClearButton(props: MobileToolbarButtonProps) {
  const { controller, Wrapper, item, locale } = props;
  const handleClick = React.useCallback(() => {
    controller.run('onAction', clearStyles());
  }, [controller]);

  return (
    <Wrapper type={'horiz'} item={item} width={2.1} onClick={handleClick}>
      <div>{locale.buttonTitle}</div>
    </Wrapper>
  );
}

export function MobileClearButtonBig(props: MobileToolbarButtonProps) {
  const { controller, Wrapper, item } = props;
  const handleClick = React.useCallback(() => {
    controller.run('onAction', clearStyles());
  }, [controller]);

  return (
    <Wrapper type={'big'} icon="clearStyle" item={item} onClick={handleClick} />
  );
}
